input{
    padding: 15px;
    margin: 10px 0px;
    border-radius: 5px;
    border: 1px solid gray;
}

.formInput{
    display : flex;
    flex-direction: column;
    width: 280px;
}

.label{
    font-size: 13px;
    color: gray;
}

span{
    font-size : 12px;
    padding: 3px;
    color: red;
    display: none;
}

input:invalid ~span{
   display: block; 
}